<template>
    <div class='header' @mouseleave="leave">
        <div class='headRight' @click='showModal'>
            <img :src="back" class='back'/>
            {{name}}<img src='../assets/images/btn_user_aroow_down.svg' class='arrowImg'/>
            <!-- <el-dropdown  class="el-dropdown-link" @command="loginOut">
              <span>
                  <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item>个人设置</el-dropdown-item>
                  <el-dropdown-item>退出</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown> -->
            <!-- <div class="arrow"></div> -->
            <!-- <el-popover
              placement="bottom"
              title="标题"
              width="200"
              trigger="click"
              content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。">
              <el-button slot="reference">click 激活</el-button>
            </el-popover> -->
        </div>
        <div class="arrow" v-if='show' id='arrow' @mouseenter.stop="enter">
          <div @click="personSet" class='fir'>个人设置</div>
          <div @click="loginOut">退出</div>
        </div>
    </div>
</template>
<script>
import {FitLoginout} from '../services/service.js'
import { setTimeout } from 'timers';
import {isApiResponseOk, getSessionStorage} from '../utils/com.js'
export default {
  data () {
    return {
      name: 'name',
      back: require('../assets/images/img_user_pic_deflut.png'),
      down: require('../assets/images/btn_user_aroow_down.svg'),
      up: require('../assets/images/btn_user_aroow_up.svg'),
      show: false
    }
  },
  mounted () {
  	let personInfo = getSessionStorage('userInfo')
    this.name = personInfo.name
    if (localStorage.headImage === 'null') {
    } else if (localStorage.headImage) {
      this.back = localStorage.headImage
    }
  },
  methods: {
    loginOut () {
      let self = this
      self.show = false
      this.$confirm('确认退出系统吗？', {type: 'warning'})
        .then(_ => {
            self.$store.commit('clearCache', '')
	        localStorage.setItem('access_token', '')
	        localStorage.setItem('time', '')
	        localStorage.setItem('refresh_token', '')
	        self.$router.push({name: 'login'})
        })
        .catch(_ => {})
    },
    showModal () {
      this.show = !this.show
    },
    leave (event) {
      if (!this.show) return
      var div = document.getElementById('arrow');
      setTimeout(_=>{
        var x = event.clientX;
        var y = event.clientY;
        var divx1 = div.offsetLeft;
        var divy1 = div.offsetTop;
        var divx2 = div.offsetLeft + div.offsetWidth
        var duvy2 = div.offsetTop + div.offsetHeight
        // console.log(x, divx1, divx2)
        if (x < divx1 || x > divx2 || y < divy1 || y > divy2) {
          // 如果离开的话，则执行
          console.log('shuju')
          this.show = false
        }
      }, 1000)
    },
    enter (event) {
      // this.show = true
    },
    personSet () {
      // 跳转至个人设置
      this.show = false
      this.$router.push({name: 'personSet'})
    }
  }
}
</script>
<style lang="sass" scoped>
.header
    width: 100%;
    //height: 100%;
    display: flex;
    align-items: center;
    .headLeft
        height: 100%;
        vertical-align: middle;
        background-image: linear-gradient(-123deg, #20C6BA 10%, #00D3FF 100%);
        display: flex;
        align-items: center;
        justify-content: center;
        img
            width: 4.1rem;
    .headRight
        float: right;
        position: absolute;
        right: .37rem;
        top: 0rem;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        height: 100%;
        .back
            width: .48rem;
            height: .48rem
            border-radius: 50%;
            margin-right: .24rem;
        .arrowImg
          width: .12rem;
          height: .75rem;
          margin-left: .2rem;
</style>
<style lang="scss" scoped>
.arrow{
    width: 150px;
    height: 100px;
    background-color: #fff;
    position: relative;
    // border: 2px solid #333;
    // top: .85rem;
    z-index: 100;
    position: fixed;
    right: 37px;
    top: .8rem;
    box-shadow: 0 6px 12px 0 rgba(0,0,0,0.15);
    div{
      font-family: PingFangSC-Medium;
      font-size: .14rem;
      color: #263238;
      letter-spacing: 0;
      height: 50px;
      line-height: 50px;
      text-align: center;
      margin-left: 12px;
      margin-right: 12px;
      cursor: pointer;
    }
    .fir{
      border-bottom: 1px solid rgba(216,216,216,.3);
    }
}
.arrow:before{
    content: "";
    border-bottom: 9px solid white;
    border-left: 9px solid transparent;
    border-right: 9px solid transparent;
    position: absolute;
    bottom:100%;
    left: 67px;
}
.arrow:after{
    content: "";
    border-bottom: 8px solid white;
    border-left:8px solid transparent;
    border-right: 8px solid transparent;
    position: absolute;
    bottom:100%;
    left: 69px;
  }
</style>




