<template>
    <div class='leftMenu'>
        <el-row class="tac">
                <div class='headLeft' style="color: #fff;">
                	学生选课系统
                    <!--<img src='../assets/images/bg_gym_login.png'/>-->
                </div>
                <el-menu
                    :default-active="routers"
                    class="el-menu-vertical-demo"
                    background-color="#263238"
                    active-text-color="#409EFF"
                    text-color="#8597a1"
                    >
                    <el-menu-item :index="item.name" @click = "goCourse(item.name)" v-if='!item.submemu'  v-for = "(item,index) in menuList"
                    :key='index'>
                        <template slot="title">
                            <i class='iconfont' :class='item.className'></i>
                            <span :class="{'is-active': item.name == routers}">{{item.text}}</span>
                        </template>
                      </el-menu-item>
                </el-menu>
        </el-row>
    </div>
</template>
<script>
import {getSessionStorage} from '../utils/com.js'
export default {
  data () {
    return {
      menuList: [],
      routers: this.$route.name
    }
  },
  watch: {
    $route(to,from){
        this.routers = to.name
    }
  },
  computed () {
      
  },
  created () {
  	let userType = getSessionStorage('userInfo').userType
  	if (userType == 1) {
  		this.menuList = [
	      {summenu: false, name: 'student', text: '学生管理', className: 'icon-icon_menu_user'},
	      {summenu: false, name: 'courseList', text: '课程管理', className: 'icon-icon_menu_class'},
	      {summenu: false, name: 'teacher', text: '教师管理', className: 'icon-icon_menu_coach1'},
	      {summenu: false, name: 'selectCourseList', text: '排课管理', className: 'icon-menu_work'},
	    ]
  	} else if (userType == 3){
  		this.menuList = [
	      {summenu: false, name: 'student', text: '学生管理', className: 'icon-icon_menu_user'},
	      {summenu: false, name: 'courseList', text: '课程管理', className: 'icon-icon_menu_class'}
	    ]
  	} else {
  		this.menuList = [
	      {summenu: false, name: 'studentSelectCourse', text: '选课信息', className: 'icon-menu_data'}
	    ]
  	}
  },
  methods: {
    // 跳转链接
    goCourse (item) {
      this.$router.push({path: item})
    }
  }
}
</script>
<style lang="sass" scoped>
.leftMenu
    background: #263238;
    height: 100%;
    .tac
        height: 100%;
        .el-menu
            border-right: 1px solid #263238;
            padding-top: 21.1%;
            background-color: #263238 !important;
            i
                margin-right: .2rem;
                margin-left: .4rem;
                color: rgb(133, 151, 161);
            .el-submenu
                background-color: #263238 !important;
                font-weight: bold;
            .is-active
                color: rgb(231, 235, 240) !important;
                background-color: #20292E !important;
            img
                width: .36rem;
                margin-right: .2rem;
            .el-menu-item-group__title
                padding: 0px !important;
        .el-menu-item-group
            background-color: #20292E !important;
            .el-menu-item-group__title
                padding: 0px !important;
            .el-menu-item
                background-color: #20292E !important;
                color: #8597a1 !important;
                margin-left: .6rem !important;
                position: relative;
            .is-active
                color: #fff !important;
                &:before
                    content: '|';
                    margin-right: 8px;
                    font-size: 12px;
                    position: absolute;
                    left: 26px;
                    top: -1px;
            .el-menu-item-group__title
                padding: 0px !important;
            .el-submenu .el-menu-item
                height: .62rem;
                line-height: .62rem;            
    .headLeft
        height: .7rem;
        vertical-align: middle;
        background-image: linear-gradient(-123deg, #20C6BA 10%, #00D3FF 100%);
        display: flex;
        align-items: center;
        justify-content: center;
        img
            width: 2.1rem;
    .el-menu
        height: calc(100% - .7rem);
        height: -moz-calc(100% - .7rem);
        height: -webkit-calc(100% - .7rem);
        overflow-y: auto;
        overflow-x: hidden;
    ::-webkit-scrollbar
            width: 4px;  
            height: 16px;  
            background-color: #dcdfe6;
    /*定义滚动条轨道 内阴影+圆角*/  
    ::-webkit-scrollbar-track 
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
        border-radius: 4px;  
        background-color: #fff;   
        
    /*定义滑块 内阴影+圆角*/  
    ::-webkit-scrollbar-thumb  
        border-radius: 10px;  
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
        background-color: #fff；
</style>



