<template>
    <div class='main'>
        <el-row :gutter="16" class='main_parent'>
        <el-col :span="3" class='left_Menu'><vleft></vleft></el-col>
        <el-col :span="21">
           <div class='head'>
              <vhead></vhead>
            </div>
            <div class='content-main'>
               <router-view></router-view>
            </div>
        </el-col>
      </el-row>
    </div>
</template>
<script>
import vleft from '@/components/leftMenu.vue'
import vhead from '@/components/head.vue'
export default {
  data () {
    return {
    }
  },
  components: {
    vleft,
    vhead
  }
}
</script>
<style lang="sass" scoped>
.main
    .main_parent
        height: 100%;
        position: absolute;
        width: 100%;
        .el-col-21
            padding: 0px !important;
            height: 100%;
            position: relative;
            overflow-y: auto;
            background: #f3f7f9;
    .left_Menu
        background: #545c64;
        height: 100%;
        padding: 0px !important;
    .left
        width: 100%;
        height: 100%;
    .head
        position: absolute;
        width: 100%;
        height: .7rem;
        background: #FFF;
        line-height: .7rem;
    .content-main
        margin-top: .7rem;
        // height: -moz-calc(100% - .7rem);
        // height: -webkit-calc(100% - .7rem);
        // height: calc(100% - .7rem);
        padding: 12px;
        width: 100%;
        padding: 0.38rem 0.5rem;
        position: absolute;
        background: #f3f7f9;
</style>



